* {
    margin: 0;
    padding: 0;
}

body {
    background: #2c3e50;
    color: #ecf0f1;
}


div.mid {
    align-content: center;
    position:absolute;

}
#palyers_zone{
    position:absolute;

    width: 1000px;  
    height: 100px;
    white-space:nowrap;

}
#player_me,
#player_yard {
    position:absolute;

    border-style: groove;
    border-width: 3px;
    border-radius: 5px;
    width: 1000px;
    margin-top: 10px;
}
#player_me{
    height: 270px;
    top: 350px;
}
#player_yard {
    height: 200px;
    top: 100px;

}
div.player_menu{
    width: 300px;  
    height: 60px;
}
div.player_ot {
    margin: 5px;
    border-style: groove;
    border-width: 2px;
    border-radius: 10px;
    width: 160px;
    height: 90px;
    padding-left: 5px;
    float: left;
}
#cur_mani{
    position:absolute;
    left: 5px;
    margin: 5px;
    width: 150px;
}

#cur_card{
    position:absolute;
    left: 100px;
    margin-left: 50px; 
}




.cardsel,
.card {
    position: relative;
    height: 90px;
    float: left;
    border-radius: 10px;
    -webkit-box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.3);
    box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.3);
    transition: all 0.5s;

}
.cardsel {
    width: 64px;

    color: #f1c40f;
    background:#2980b9;
    margin-left: 1px;
    margin-right: 1px;

    margin-top: 15px;

}
.card {
    width: 62px;
    color: black;
    background: #ecf0f1;
    margin-left: 2px;
    margin-right: 2px;
    margin-top: 15px;

}


.cardsel:hover,
.card:hover {
    cursor: pointer;
    /* transform: scale(1.05); */
}


.cardsel p,
.card p {
    text-align: center;
    vertical-align: middle;
    font: 30px/90px Georgia, Times New Roman, serif;
}

.suitdiamonds:before,
.suitdiamonds:after {
    content: "♦";
    color: #ff0000;
}

.suitdiamonds:before,
.suitdiamonds:after {
    content: "♦";
    color: #ff0000;
}

.suithearts:before,
.suithearts:after {
    content: "♥";
    color: #ff0000;
}

.suitclubs:before,
.suitclubs:after {
    content: "♣";
    color: #000;
}

.suitspades:before,
.suitspades:after {
    content: "♠";
    color: #000;
}

div[class*='suit']:before {
    position: absolute;
    font-size: 20px;
    left: 2px;
    top: 0px;
}

div[class*='suit']:after {
    position: absolute;
    font-size: 20px;
    right: 2px;
    bottom: 0px;
}


button { /* 按钮美化 */
	width: 90px; /* 宽度 */
	height: 60px; /* 高度 */
	border-width: 0px; /* 边框宽度 */
	border-radius: 3px; /* 边框半径 */
	background: #1abc9c; /* 背景颜色 */
	cursor: pointer; /* 鼠标移入按钮范围时出现手势 */
	outline: none; /* 不显示轮廓线 */
	font-family: Microsoft YaHei; /* 设置字体 */
	color: white; /* 字体颜色 */
    font-size: 20px; /* 字体大小 */
    margin: 10px;
    transition: all 0.5s;

}
button:hover { /* 鼠标移入按钮范围时改变颜色 */
    background: #16a085;
    transform: scale(1.1);

}


ul {
    list-style: none;
}

.drop-box {
    box-sizing: border-box;
    width: 100px;
    font-size: 14px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.175);
    overflow: hidden;
    margin-bottom: 5px;
    float: right;
}

.drop-box p {
    height: 24px;
    padding-top: 5px;
    position: relative;
    z-index: 10;
    margin: 0px;
}

.drop-box p:after {
    /*三角箭头*/
    position: absolute;
    top: 10px;
    right: 10px;
    width: 0px;
    height: 0px;
    content: '';
    border: 6px solid transparent;
    border-top-color: #E4E4E3;
}

.drop-box .menu {
    display: none;
    background-color: #2980b9;
}

.drop-box .menu li {
    height: 35px;
    line-height: 35px;
}

.drop-box .menu li:hover {
    background-color: #fee5ca;
    color: #fda653;
}

.drop-box .menu .dp-active {
    display: none;
}

.drop-box:hover {
    cursor: pointer;
}

.drop-box:hover .menu {
    display: block;
}

.drop-box:hover p:after {
    top: 5px;
    border: 6px solid transparent;
    border-bottom-color: #E4E4E3;
}
